/**
 * 无论是否受权限控制的路由信息都要先确定下来, 
 * 然后根据路由权限控制接口的 name 匹配当前路由的 name 确定路由是否需要绑定注册
 */
import Vue from 'vue'
import Router from 'vue-router'
import qs from "qs"

import constantRouter, {
  notFoundRouter
} from "./constantRouter"

import authorityRouter from "./authorityRouter"

export default function createRouter({
  store,
  NProgress
}) {
  Vue.use(Router);

  const router = new Router({
    scrollBehavior: () => ({
      y: 100
    }),
    routes: constantRouter
  });

  if (process.env.NODE_ENV == "development") {
    //开发环境
    store.dispatch('setRouteItemAsync').then(() => {
      router.addRoutes([...authorityRouter, notFoundRouter]);
    })
    router.beforeEach((to, from, next) => {
      NProgress.start();
      next();
    });
  } else {
    router.beforeEach((to, from, next) => {
      NProgress.start();
      if (!store.state.routeDone) {
        store.dispatch('setRouteItemAsync').then(() => {

          //#region 遍历路由
          let routerNames = [],
            routerUrlMeta = {};
          store.state.routeItem.forEach((item, index) => {
            (item.children || []).forEach((item, index) => {
              //获取所有授权路由的name
              routerNames.push(item.name);

              if (item.icon) {
                // 将第三方路由信息(href)保存到RouteUrlMeta
                //备注, 里面包括icon, project信息
                const summary = JSON.parse(item.icon || "{}");
                routerUrlMeta[item.name] = {
                  name: item.name,
                  project: summary.project,
                  href: item.href
                }
              }
            });
          });
          store.commit("setRouteUrlMeta", routerUrlMeta);
          //#endregion

          //#region 根据meta.name, 绑定当前项目路由
          let routers = [];
          authorityRouter.forEach((item, index) => {
            if (item.meta && item.meta.name && routerNames.indexOf(item.meta.name) > -1) {
              routers.push(item);
            }
          });
          //#endregion

          //最后添加notFound
          routers.push(notFoundRouter)
          router.addRoutes(routers);

          //继续执行路由
          next({
            ...to
          });
        }).catch(() => {
          //如果报异常就直接404
          router.addRoutes([notFoundRouter])
        })
      } else {
        //如果路由地址在当前项目中没有发现, 继续需找存储的第三方路由信息
        if (to.name === "NoFound") {
          let path = to.path.replace("/", ""),
            routeUrlMetaItem = store.state.routeUrlMeta[path];

          //将第三方页面嵌入到iframe
          if (routeUrlMetaItem && routeUrlMetaItem.project !== store.state.project) {
            store.commit("isIframeOuter", true);
            let href = `${routeUrlMetaItem.href}?${qs.stringify(to.query)}` || `/${routeUrlMetaItem.project}/#${to.fullPath}`
            store.commit("setIframeOuterUrl", href);
            return next();
          }
        }
        store.commit("isIframeOuter", false);
        next();
      }
    })
  };

  router.afterEach(() => {
    NProgress.done();
  });

  return router;
}



