import axios from "axios"
import {
  auth
} from "@/api"

export default {
  
  /**获取菜单/路由信息 */
  async setRouteItemAsync({
    commit
  }) {
    let menu = [];
    try {
      let result = await axios.get(auth.menu);
      if (result && result.data) {
        menu = result.data;
      }
    } catch (ex) {
      console.error(ex);
    }
    commit("setRouteItem", menu);
    commit("setRouteDone", true);
  },
}



