/**
 * 需要通过权限控制的路由
 */
import main from '../views/main'

export default [{
  path: '/example',
  meta: {
    name: 'example'
  },
  component: main,
  children: [{
    name: 'example',
    path: '/',
    component: resolve => require(['../views/example'], resolve)
  }]
}]



