<template>
  <header class="header">
    <div class="logo">
      <span :class="{collapse : isCollapse}">{{projectName}}</span>
      <i class="icon el-icon-android-menu" @click="collapse"></i>
    </div>
    <div class="userinfo">
      <el-dropdown trigger="hover">
        <span class="el-dropdown-link userinfo-inner">
          {{userInfo.name}}
          <i class="avatar" v-if="userInfo.avatar" :style="{backgroundImage:`url(${userInfo.avatar})`}">&nbsp;</i>
          <i v-else class="avatar">{{ userInfo.name[0] }}</i>
        </span>
        <el-dropdown-menu slot="dropdown">
          <el-dropdown-item>我</el-dropdown-item>
          <el-dropdown-item divided @click.native="logout">退出登录</el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>
    </div>
  </header>
</template>
<script>
import { auth } from "@/api"
export default {
  name: "header",
  data() {
    return {
      userInfo: {
        name: ""
      }
    }
  },
  computed: {
    isCollapse() {
      return !!this.$store.state.appLayoutMenuCollapse;
    },
    projectName() {
      return this.$store.state.projectName;
    }
  },
  methods: {
    async logout() {
      try {
        await this.$confirm('您确认登出当前账号吗?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消'
        });
        try {
          let result = await this.axios.get(auth.logout);
          if (result && result.data) {
            location.href = result.data.url;
          }
        } catch (ex) {
          console.error(ex)
          this.$message({
            message: "登出异常",
            type: "warning"
          })
        }
      } catch (ex) {
        console.error(ex);
      }
    },
    async getUserInfo() {
      try {
        let result = await this.axios.get(auth.user);
        if (result && result.data) {
          if (result.data.loginUrl && result.data.loginUrl != "") {
            location.href = result.data.loginUrl;
          }
          this.userInfo.name = result.data.username || "某某";
          this.userInfo.avatar = result.data.avatar;
        }
      } catch (ex) {
        this.userInfo.name = "某某"
        console.error(ex)
      }
    },
    collapse() {
      this.$store.commit("setAppLayoutMenuCollapse", !this.isCollapse);
      localStorage.setItem("appLayoutMenuCollapse", this.isCollapse);
    }
  },
  mounted() {
    this.getUserInfo();
    const appLayoutMenuCollapse = localStorage.getItem("appLayoutMenuCollapse");
    this.$store.commit("setAppLayoutMenuCollapse", JSON.parse(appLayoutMenuCollapse || "false"));
  }
}
</script>
<style lang="less" scoped>
@import "./header.less";
</style>



