/**
 * 不需要权限控制的路由
 */
import main from '../views/main'

export default [{
  path: '/(about)?',
  meta: {
    name: 'about'
  },
  component: main,
  children: [{
    name: 'about',
    path: '/',
    component: resolve => require(['../views/about'], resolve)
  }]
}];

export const notFoundRouter = {
  path: `*`,
  name: "NoFound",
  component: resolve => require(['../views/notFound'], resolve)
};



