<template>
  <el-menu :router="true" :default-active="$route.path.replace('/', '')" :collapse="isCollapse" @select="menuSelect" class="layout-menu">
    <el-submenu v-for="(item, index) in menuData" :key="index" :index="index + ''" v-if="item.children">
      <template slot="title">
        <i :class=" ['el-icon', item.icon || 'el-icon-menu']"></i>
        <span slot="title" class="submenu-title">{{item.title}}</span>
      </template>
      <el-menu-item v-for="(menuItem, menuIndex) in item.children" :index="menuItem.name" :key="menuIndex" :route="{path: '/' + menuItem.name}" @click.native="menuClick(menuItem)">{{menuItem.title}}</el-menu-item>
    </el-submenu>
  </el-menu>
</template>
<script>
export default {
  name: "menu",
  data() {
    return {
      openPath: ""
    }
  },
  computed: {
    menuData() {
      let routeItem = this.$store.state.routeItem;
      let menus = [];
      routeItem.forEach((item) => {
        if (Array.isArray(item.children) && item.children.length) {
          let menu = {
            title: item.title,
            icon: item.icon,
            children: []
          }

          item.children.forEach((childItem) => {
            let extendInfo = JSON.parse(childItem.icon || "{}");
            if (extendInfo.show !== false) {
              menu.children.push(childItem);
            }
          });
          if (menu.children.length) {
            menus.push(menu);
          }
        }
      });
      return menus;
    },
    isCollapse() {
      return !!this.$store.state.appLayoutMenuCollapse;
    }
  },
  methods: {
    menuSelect(indexPath) {
    },
    menuClick({ name }) {
      console.log(this.openPath, name)
      if (this.openPath === name) {
        location.reload();
      }
      this.openPath = name;
    }
  },
  mounted() {
    this.openPath = this.$route.path.replace('/', '');
  }
}
</script>
<style lang="less" scoped>
@import "./menu.less";
</style>





